footer {
  background-color: #262626;
  color: #fff;
  border-top: 2px solid #000;
  padding: 30px 10px; }

footer .row {
  max-width: 1140px;
  margin: auto; }

footer .row .brand-name {
  font-family: 'ZCOOL XiaoWei', cursive;
  text-align: center;
  color: #ffffff;
  font-size: 36px;
  font-weight: 600; }

.footer-title {
  color: #e13c31;
  font-size: 24px;
  text-align: center; }

.footer-brand-text {
  text-align: center; }

.footer-contacts {
  list-style-type: none;
  text-align: center;
  padding: 0;
  margin: 20px 0 0 0; }

.footer-contacts li {
  padding: 5px; }

.footer-links {
  list-style-type: none;
  margin: 0;
  padding: 0; }

.copy-right {
  background-color: #000;
  text-align: center;
  color: #fff;
  height: 30px;
  padding-top: 10px; }

#cookie {
  display: block;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0; }

#cookie .card {
  border: 3px solid #000;
  background: #fff; }

#cookie .card button {
  display: block;
  width: 100px;
  height: 40px;
  background-color: #e13c31;
  padding: 0;
  border: 0;
  font-weight: 650;
  font-size: 20px;
  border-radius: 5px; }

@media screen and (min-width: 786px) {
  .topnav a {
    display: block; }
  .topnav a.active {
    color: #e13c31; }
  .topnav .menu-icon {
    display: none; }
  .topnav a:hover {
    border-bottom: 2px solid #fff; }
  .topnav a.active:hover {
    border-bottom: 2px solid #e13c31; }
  .topnav .bookmarks {
    float: right; }
  #cookie {
    right: 50%; } }

.team-pic,
